Vue.filter('dateFormat', (originVal) => {
  const dt = new Date(originVal)
  const y = dt.getFullYear()
  const m = (dt.getMonth() + 1 + '').padStart(2, '0')
  const d = (dt.getDate() + 1 + '').padStart(2, '0')
  const hh = (dt.getHours() + 1 + '').padStart(2, '0')
  const mm = (dt.getMinutes() + 1 + '').padStart(2, '0')
  const ss = (dt.getSeconds() + 1 + '').padStart(2, '0')
  return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
})
Vue.filter('timeFormat',(time) =>{
  var hours = Math.floor(time / 3600)
  var minutes = Math.floor(time %3600 /60 )
  var seconds = time % 60
  minutes = minutes>10? minutes:'0'+minutes
  seconds = seconds>10 ? seconds:'0'+seconds
  return `${hours}:${minutes}:${seconds}`
})
Vue.filter('messageType',(type) =>{
  let message =''
  switch (type) {
      case 0:  message="";break;
    case 1: message='';break;
      case 2: message='';break;
    case 3: message='';break;
    case 4: message='';break;
    case 5:message= '';break;

    default:
      break;
  }
 return message

} )
Vue.filter('dataFilter',(value)=>{//valuejson
  var year = 24*60*60*1000*365;//
  var month = 24*60*60*1000*30;//
  var day = 24*60*60*1000;//
  var hour = 60*60*1000;//
  var minute = 60*1000;//

   //getTime()    1970  1  1 
  var newDate = new Date().getTime();//197011
  var time = new Date(value).getTime();//json1970.1.1json
  var date = newDate - time;//

 //
  var years = parseInt(date/year)>0 ? `${parseInt(date/year)}` : ""; //0,,
  var months = parseInt(date/month)>0 ? `${parseInt(date/month)}` : "";//0,,
  var days = parseInt(date/day)>0 ? `${parseInt(date/day)}` : "";//0,,
  var hours = parseInt(date/hour)>0 ? `${parseInt(date/hour)}` : "";//0,,
  var minutes = parseInt(date/minute)>0 ? `${parseInt(date/minute)}` : "";//0,,


  if(years.length != 0){
    return years;
  } else if(months.length != 0){
    return months;
  } else if(days.length != 0){
    return days;
  } else if(hours.length != 0){
    return hours;
  } else if(minutes.length != 0){
    return minutes;
  } else {
    return parseInt((date)/1000)>0?`${parseInt((date)/1000)}`:"";
  }
}

)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


const headerEl = document.querySelector("header");
const glide = new Glide(".glide", {
  type: "carousel",
  startAt: 0,
  autoplay: 3500,
});
const captionsEL = document.querySelectorAll(".slide-caption");

glide.on(["mount.after", "run.after"], () => {
  const caption = captionsEL[glide.index];
  anime({
    targets: caption.children,
    opacity: [0, 1],
    duration: 400,
    easing: "spring(1, 80, 10, 0)",
    delay: anime.stagger(400, { start: 300 }),
    translateY: [anime.stagger([40, 10]), 0],
  });
});
glide.on("run.before", () => {
  document.querySelectorAll(".slide-caption > *").forEach((el) => {
    el.style.opacity = 0;
  });
});

glide.mount();


// 
const staggeringOption = {
  delay: 300,
  distance: "50px",
  duration: 500,
  easing: "ease-in-out",
  origin: "bottom",
};

// interval
ScrollReveal().reveal(".feature", { ...staggeringOption, interval: 350 });

ScrollReveal().reveal(".service-item", { ...staggeringOption, interval: 350 });

const dataSectionEl = document.querySelector(".data-section");

ScrollReveal().reveal(".data-section", {
  beforeReveal: () => {
    anime({
      targets: ".data-piece .num",
      innerHTML: (el) => {
        return [0, el.innerHTML];
      },
      duration: 1500,
      round: 1,
      easinge: "easeInExpo",
    });
    dataSectionEl.style.backgroundPosition =
      "center calc(50% - ${dataSectionEl.getBoundingClientRect().bottom/5}px)";
  },
});



