<template>
  <div>
    <section id="about-us" class="about-us">
      <h2 class="title-us"></h2>
      <div class="intro">
        <p>
          
        </p>
      </div>
      <div class="features">
        <div class="feature">
          <i class="fas fa-lightbulb"></i>
          <h4 class="feature-title"></h4>
          <p class="feature-content">
             
          </p>
        </div>
        <div class="feature">
          <i class="fas fa-chart-line"></i>
          <h4 class="feature-title"></h4>
          <p class="feature-content">
            ,
          </p>
        </div>
        <div class="feature">
          <i class="fas fa-shopping-cart"></i>
          <h4 class="feature-title"></h4>
          <p class="feature-content">
            
          </p>
        </div>
        <div class="feature">
          <i class="fas fa-desktop"></i>
          <h4 class="feature-title"></h4>
          <p class="feature-content">
            
          </p>
        </div>
        <div class="feature">
          <i class="fas fa-tachometer-alt"></i>
          <h4 class="feature-title"></h4>
          <p class="feature-content">
            
          </p>
        </div>
        <div class="feature">
          <i class="fas fa-server"></i>
          <h4 class="feature-title"></h4>
          <p class="feature-content">
            
          </p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    name: "AboutUs",
    mounted() {
      // 
          const staggeringOption = {
            delay: 300,
            distance: "50px",
            duration: 500,
            easing: "ease-in-out",
            origin: "bottom",
          };

      // interval
          ScrollReveal().reveal(".feature", { ...staggeringOption, interval: 350 });
    }
  };
</script>

<style scoped>
  section {
    /*  */
    display: grid;
    /*  */
    justify-items: center;
    max-width: 1280px;
    padding: 0 80px;
  }
  /*  */
  .about-us {
    padding-bottom: 32px;
  }

  .features {
    display: grid;
    /*   1fr1/3*/
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 126px 126px;
    column-gap: 5vw;
  }

  .feature {
    display: grid;
    grid-template-areas:
      "icon title"
      "icon content";
    grid-template-columns: 60px 1fr;
    grid-template-rows: 1fr 3fr;
  }

  .feature i.fas {
    grid-area: icon;
    font-size: 34px;
    color: #ff434f;
  }

  .feature-title {
    grid-area: title;
    font-size: 18px;
    color: #2e2e2e;
  }

  .feature-content {
    grid-area: content;
    color: #8b8b8b;
    margin-top: 8px;
  }
  .title-us {
    font-size: 34px;
    color: #2e2e2e;
  }

  .title-us::after {
    content: "";
    display: block;
    width: 80%;
    height: 4px;
    background-color: #ff434f;
    margin-top: 14px;
    transform: translateX(10%);
  }
  .intro {
    margin: 28px 0 60px 0;
    /* text-indent: 2em; */
    font-size: 18px;
    color: #727272;
  }

  @media (max-width: 992px) {
    .features {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: unset;
    }
  }

  @media (max-width: 768px) {
    section {
      padding: 0 40px;
    }

    .features {
      grid-template-columns: 1fr;
    }
  }
</style>



